/* Main Brand Colors *****************************************/

   $blue:    		#1874dc; // Interface, Hyperlinks, UI Button
   $blue-dark:    	#002856; 
   $purple:  		#8332a7; 
   $purple-dark:  	#490e67;
   $red:     		#c10f3a; // Accent, Alert, St. Jude Logo
   $red-dark:     	#911938;
   $orange:  		#e03400; // Error Message
   $yellow:  		#ffd100; // Warning
   $yellow-dark:  	#ffb125;
   $green:   		#c0da07; // Success, Action Button
   $green-dark:   	#75bc21;
   $aqua:   		#64d0e4; 
   $aqua-dark:   	#1eadc1;
   $gray:   		#cccccc;
   $gray-dark:   	#63666b;

